
.ui-count-down-button {
  padding: 1em 2em;
  border-radius: 0.4em;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
}
.ui-count-down-button.enable {
  background-color: #39f;
  cursor: pointer;
  color: #fff;
}
.ui-count-down-button.enable:hover {
  background-color: #38e;
}
.ui-count-down-button.disable {
  background-color: #ccc;
  color: #555;
  cursor: not-allowed;
}



